.services {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 3.7rem;
}

.services-header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.services-header>h1 {
  margin-bottom: 20px;
  font-size: 3.5rem;
  font-family: var(--primaryFont);
}

.services-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 2rem;
}

.services-body>p {
  font-family: var(--primaryFont);
  font-weight: 400;
  font-size: 0.75rem;
  width: 60%;
  text-align: center;
  margin-bottom: 6rem;
}

.services-bodycontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4.5rem;
  width: 100%;
  flex-wrap: wrap;
}

/*  ------------------------------------ MEDIA QUERIES ------------------------------------ */
@media (min-width: 992px) and (max-width: 1380px) {
  .services {
    padding: 2.7rem;
  }

  .services-body>p {
    width: 70%;
  }
}

@media screen and (max-width: 992px) {
  .services {
    padding: 1.7rem;
  }

  .services-body>p {
    font-size: 1.25rem;
    width: 85%;
    text-align: center;
    margin-bottom: 4rem;
  }

  .services-bodycontainer {
    gap: 2.5rem;
  }
}

@media screen and (max-width: 800px) {
  .services {
    padding: 1rem;
  }

  .services-header h1 {
    font-size: 3rem;
    margin-bottom: 30px;
  }

  .services-body>p {
    font-size: 1.05rem;
    width: 95%;
    text-align: center;
    margin-bottom: 3rem;
  }

  .services-bodycontainer {
    gap: 2rem;
  }
}

@media screen and (max-width: 600px) {
  .services {
    padding: 0.5rem;
  }

  .services-header h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
